.container {
  min-height: 100vh;
  width: 100vw;
  //background-image: linear-gradient(to top right, #3629a7, #343a40);
  position: relative;
  z-index: 2;

  form {
    z-index: 2;
    display: flex;
    flex-direction: column;
    background-color: white;
    width: 300px;
    height: fit-content;
    border-radius: 10px;
    box-shadow: 0 70px 140px 0 rgba(130, 136, 171, .2);
    padding: 25px 40px 40px 40px;
    margin-bottom: 20px;

    @media (max-width: 431px) {
      width: calc(90% - 80px);
    }

    h3 {
      margin: 0;
      font-family: Playfair Display, serif;
      font-size: 45px;
    }

    .subtitle {
      font-size: 12px;
      line-height: 2;
      color: #757c98;
    }
  }

  >img {
    top: 0;
    position: absolute;
    z-index: 3;
    left: 50%;
    min-width: 100%;
    height: 400px;
    max-height: 30%;
    overflow: hidden;
    transform: translateX(-50%);
    display: block;
  }

  .contact {
    color: white;
    display: flex;
    flex-direction: column;

    >:nth-child(1),
    >:nth-child(2) {
      display: flex;
      flex-direction: row;
    }
    img {
      width: 50px;
      height: 50px;
      margin: 0 10px;
    }
  }
  .form {
    position: absolute;
    top: 30%;
    left: calc(50% - 190px);

    display: flex;
    flex-direction: row;

    @media (max-width: 431px) {
      left: 5%;
    }

    @media (max-width: 960px) {
      flex-direction: column;
    }
  }
}