.homeSection {
  height: 100vh;
  width: 100vw;
  /*overflow: hidden;*/

}

.particles {
  background-image: linear-gradient(to top right, #3629a7, #343a40);
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
}

.intro {

  font-size: 32pt;
  line-height: 36pt;
  text-align: center;
  width: fit-content;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

}

.line {
  width: 710px;
  background-color: #BBD336;
  height: 3px;
  padding: 0;
}


.heroText {

  :first-child {
    color: #ea2c62;
  }

  :nth-child(3) {
    height: 100px;
    color: #d37815;
  }

  //border: 1px solid;
  position: absolute;
  top: 30%;
  left: calc((100% - 710px) / 2);
  z-index: 2;

  h1 {
    margin: 10px 0 20px 0;
    letter-spacing: -1px;
    font-family: Playfair Display, serif;
    font-weight: 700;
    font-size: 55px;
    line-height: 1;
    text-align: center;
    color: #fff;
  }
}

.HeaderWhiteMask {
  height: 100vh;
  width: 100vw;
  position: relative;
  z-index: 2;


  img {
    bottom: 0;
    position: absolute;
    z-index: 1;
    left: 50%;
    height: 400px;
    max-height: 40%;
    overflow: hidden;
    display: block;
    transform: translateX(-50%);
  }
}


@media screen and (max-width: 759px) {
  .heroText {
    left: calc((100% - 510px) / 2);

    >h1 {
      font-size: 40px;
    }
  }
  .line {
    width: 510px;
  }
}


@media screen and (max-width: 530px) {
  .heroText {
    left: calc((100% - 380px) / 2);

    >h1 {
      font-size: 30px;
    }
  }
  .line {
    width: 380px;
  }
}

@media screen and (max-width: 400px) {
  .heroText {
    left: calc((100% - 247px) / 2);

    >h1 {
      font-size: 20px;
    }
  }
  .line {
    width: 247px;
  }
}

@media screen and (max-width: 250px) {
  .heroText {
    left: calc((100% - 190px) / 2);

    >h1 {
      font-size: 16px;
    }
  }
  .line {
    width: 190px;
  }
}

.highlight {
  color: #e31b6d;
  font-family: "Raleway-semibold";
}